import lodashSplit from 'lodash/split';

import { getPlaceholderGradientColorsFromId } from '../helpers/placeholderColors';
import { getAltText, iconCN } from './helpers';

export default function Placeholder({ placeholderName, teamId, name }) {
  const placeholderColorPalette = getPlaceholderGradientColorsFromId(teamId);

  const accessibilityProps = { role: undefined as string | undefined };
  if (name) {
    accessibilityProps.role = 'img';
    accessibilityProps['aria-label'] = getAltText('Placeholder logo', name);
  } else {
    accessibilityProps['aria-hidden'] = 'true';
  }

  return (
    <span
      className={iconCN}
      {...accessibilityProps}
      style={{
        background: `linear-gradient(to bottom right, ${placeholderColorPalette.to}, ${placeholderColorPalette.from})`,
      }}
    >
      {placeholderName ? (
        lodashSplit(placeholderName, '', 1)[0].toUpperCase() // lodash allows for fancy emojis as the first letter of the username
      ) : (
        <svg
          viewBox="0 0 100 100"
          data-testid="default-logo"
          aria-hidden="true"
        >
          <use
            width="50%"
            height="50%"
            x="25%"
            y="25%"
            href="#netlify-logo-mono"
            fill="#fff"
            opacity=".75"
          />
        </svg>
      )}
    </span>
  );
}
