import { cn } from '../helpers/cn';
import { Picture } from '../Picture/Picture';
import { getAltText, imageCN, LogoSize, logoSizeCN } from './helpers';
import Placeholder from './LogoPlaceholder';

export interface LogoProps {
  /** The name of the Team/Organization that the logo is assigned to */
  name?: string;
  /** The id of the Team/Organization that the logo is assigned to */
  teamId?: string;
  /** The width and height of the logo */
  size?: LogoSize;
  /** The absolute path to the logo */
  src?: string;
  /** Class name to pass to the outermost element */
  className?: string;
  /** Determines the text to show if no logo is provided */
  placeholderName?: string;
}

export const Logo = ({
  name,
  teamId,
  size = LogoSize.Large,
  src,
  className,
  placeholderName,
}: LogoProps) => {
  return (
    <span
      className={cn(
        'tw-block tw-aspect-square tw-overflow-hidden',
        logoSizeCN(size),
        className
      )}
    >
      {src ? (
        <Picture
          alt={name ? getAltText('Logo', name) || '' : ''}
          className={imageCN}
          url={src}
          height={208}
          width={208}
          cover
        />
      ) : (
        <Placeholder
          placeholderName={placeholderName}
          teamId={teamId}
          name={name}
        />
      )}
    </span>
  );
};
