import React from 'react';

import { cn } from '../helpers/cn';
import { AccessibleIcon } from '../Icon/AccessibleIcon';
import type { IconName } from '../Icon/types';
import { useLink } from '../LinkProvider';
import { buttonCN } from './styles';
import type { ButtonLevel, ButtonVariant } from './types';

export interface ButtonIconBasicProps
  extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  onClick?: (event: React.MouseEvent<HTMLElement>) => void;
  level?: ButtonLevel;
  variant?: ButtonVariant;
  saving?: boolean;
  disabled?: boolean;
  callForAttention?: boolean;
  outset?: string;
  width?: number;
  height?: number;
  activeClassName?: string;
  iconWrapperClassName?: string;
  /** To render an HTML link. */
  to?: string;
  /** To render an HTML link. */
  href?: string;
}

export interface ButtonIconWithChildrenProps extends ButtonIconBasicProps {
  children: React.ReactNode;
  iconName?: IconName;
  title?: string;
}

export interface ButtonIconWithoutChildrenProps extends ButtonIconBasicProps {
  iconName: IconName;
  title: string;
}

export type ButtonIconProps =
  | ButtonIconWithChildrenProps
  | ButtonIconWithoutChildrenProps;

type Element = HTMLAnchorElement | HTMLButtonElement;

export const ButtonIcon = React.forwardRef<Element, ButtonIconProps>(
  (
    {
      children,
      variant,
      level,
      saving = false,
      disabled = false,
      iconName,
      title,
      callForAttention,
      outset,
      type = 'button',
      className,
      activeClassName,
      width,
      height = width,
      iconWrapperClassName = '',
      onClick,
      'aria-label': label = title,
      ...rest
    },
    ref
  ) => {
    const renderLink = useLink();

    if (!iconName && !children) {
      return null;
    }

    const CN = buttonCN({
      type: 'icon',
      level,
      variant,
      saving,
      disabled,
      callForAttention,
      outset,
      className,
    });

    const handleClick = (e) => {
      if (saving) {
        e.preventDefault();

        return;
      }

      return onClick?.(e);
    };

    const iconWrapper = (
      <span
        className={cn(
          'tw-absolute tw-left-1/2 tw-top-1/2 tw--translate-x-1/2 tw--translate-y-1/2',
          iconWrapperClassName
        )}
      >
        {iconName && title ? (
          <AccessibleIcon
            title={title}
            name={iconName}
            width={width}
            height={height}
            className="!tw-block tw-align-baseline"
          />
        ) : (
          children
        )}
      </span>
    );

    const commonProps = {
      'aria-label': label,
      className: CN,
      onClick: handleClick,
      disabled,
      ...(saving && { 'aria-disabled': true }),
      children: iconWrapper,
    };

    const { to, href } = rest;

    if (to || href) {
      return renderLink({
        ref: ref as React.ForwardedRef<HTMLAnchorElement>,
        ...(rest as React.ComponentPropsWithoutRef<'a'>),
        ...commonProps,
        activeClassName,
      });
    }

    return (
      <button
        ref={ref as React.ForwardedRef<HTMLButtonElement>}
        {...rest}
        {...commonProps}
        type={type}
      />
    );
  }
);

export default ButtonIcon;
