import { Badge, BadgeVariant } from '../Badge/Badge';
import {
  BUSINESS_NAME,
  ENTERPRISE_NAME,
  LEGACY_CREATE_PLAN_UI_NAME,
  ORB_FREE_NAME,
  ORB_GROWTH_NAME,
  ORB_PRO_NAME,
  ORB_STARTER_NAME,
  PRO_NAME,
  STARTER_NAME,
} from '../helpers/plans';

interface PlanBadgeProps {
  plan: string;
  href?: string | undefined;
  children?: JSX.Element | string | undefined;
  className?: string | undefined;
}

const SPECIAL_PLANS = [
  ORB_FREE_NAME,
  ORB_STARTER_NAME,
  ORB_PRO_NAME,
  ORB_GROWTH_NAME,
  STARTER_NAME,
  PRO_NAME,
  BUSINESS_NAME,
  ENTERPRISE_NAME,
  LEGACY_CREATE_PLAN_UI_NAME,
].map((plan) => plan.toLowerCase());

export function PlanBadge({
  plan,
  href = '',
  children = undefined,
  className = '',
}: PlanBadgeProps): JSX.Element {
  const highlight = !SPECIAL_PLANS.includes(plan.toLowerCase());

  return (
    <Badge
      variant={highlight ? BadgeVariant.Highlight : undefined}
      href={href}
      className={className}
    >
      {children || plan}
    </Badge>
  );
}
