import { forwardRef } from 'react';
import * as React from 'react';

import { cn } from '../helpers/cn';
import { DecorativeIcon } from '../Icon/DecorativeIcon';
import type { IconName } from '../Icon/types';
import { useLink } from '../LinkProvider';
import { buttonCN } from './styles';
import type {
  ButtonIconAdditionalProps,
  ButtonIconXPosition,
  ButtonLevel,
  ButtonVariant,
} from './types';

const withIconWrapperCN = cn(
  'tw-flex',
  'tw-justify-center',
  'tw-flex-nowrap',
  'tw-items-center'
);

export const render = ({
  children,
  iconName,
  iconXPosition,
  iconClassName,
  iconProps,
}: {
  iconName?: IconName;
  iconXPosition?: ButtonIconXPosition;
  iconClassName?: string;
  iconProps?: ButtonIconAdditionalProps;
  children: React.ReactNode;
}) => {
  const hasIconLeft = iconXPosition === 'left';

  if (iconName) {
    const renderIcon = (
      <DecorativeIcon
        {...iconProps}
        className={cn(
          'tw-shrink-0',
          hasIconLeft ? 'tw-mr-2' : 'tw-ml-2',
          iconProps?.className,
          iconClassName
        )}
        focusable="false"
        name={iconName}
      />
    );
    return hasIconLeft ? (
      <span className={withIconWrapperCN}>
        {renderIcon}
        {children}
      </span>
    ) : (
      <span className={withIconWrapperCN}>
        {children}
        {renderIcon}
      </span>
    );
  }

  return children;
};

interface NetlifyButtonProps {
  /* Two adjacent buttons should not have the same level */
  level?: ButtonLevel;
  variant?: ButtonVariant;
  saving?: boolean;
  disabled?: boolean;
  iconName?: IconName;
  iconXPosition?: ButtonIconXPosition;
  iconClassName?: string;
  iconProps?: ButtonIconAdditionalProps;
  outset?: string;
  type?: 'button' | 'submit' | 'reset';
  activeClassName?: string;
}

export interface ButtonProps
  extends React.ComponentPropsWithoutRef<'button'>,
    NetlifyButtonProps {}

interface AnchorProps
  extends Omit<React.ComponentPropsWithoutRef<'a'>, 'type'>,
    NetlifyButtonProps {
  /** To render an HTML link. */
  to?: string;
  /** To render an HTML link. */
  href?: string;
}

type Element = HTMLAnchorElement | HTMLButtonElement;

export type Props = ButtonProps | AnchorProps;

/**
 * For HTML buttons and links styled as buttons.
 */
export const Button = forwardRef<Element, ButtonProps | AnchorProps>(
  (
    {
      children,
      variant,
      level,
      saving = false,
      disabled = false,
      iconName,
      iconXPosition = 'left',
      iconClassName,
      iconProps,
      outset,
      type = 'button',
      className,
      activeClassName,
      onClick,
      ...rest
    },
    ref
  ) => {
    const renderLink = useLink();
    children = children && (
      <>
        {children}
        {saving && '...'}
      </>
    );

    const CN = buttonCN({
      level,
      variant,
      saving,
      disabled,
      outset,
      className,
    });

    const handleClick = (e) => {
      if (saving) {
        e.preventDefault();

        return;
      }

      return onClick?.(e);
    };

    const commonProps = {
      className: CN,
      onClick: handleClick,
      disabled,
      ...(saving && { 'aria-disabled': true }),
      children: render({
        children,
        iconName,
        iconXPosition,
        iconClassName,
        iconProps,
      }),
    };

    if (('to' in rest || 'href' in rest) && (rest.to || rest.href)) {
      return renderLink({
        ref: ref as React.ForwardedRef<HTMLAnchorElement>,
        ...rest,
        ...commonProps,
        activeClassName,
      });
    }

    return (
      <button
        ref={ref as React.ForwardedRef<HTMLButtonElement>}
        {...(rest as ButtonProps)}
        {...commonProps}
        type={type}
      />
    );
  }
);
