import type {
  Instance as TippyInstance,
  Plugin,
  Props as TippyProps,
} from 'tippy.js';

export interface TooltipPluginProps {
  hideOnPopperBlur?: boolean;
  hideOnEsc?: boolean;
  manageFocusInPopover?: boolean;
}

type Props = TippyProps & TooltipPluginProps;

const getTriggerButton = (reference: TippyInstance['reference']) => {
  if (reference instanceof HTMLButtonElement) {
    return reference;
  } else if (reference instanceof HTMLElement) {
    return reference.querySelector('button');
  }

  return null;
};

const closePopperWithoutTriggeringFocusIn = ({
  reference,
  hide,
  props,
}: TippyInstance<Props>) => {
  const button = getTriggerButton(reference);

  hide();

  if (!button || !props.trigger?.includes('focusin')) {
    return;
  }

  const preventFocusIn = (event: FocusEvent) => {
    event.stopImmediatePropagation();
  };

  button.addEventListener('focusin', preventFocusIn);
  button.focus();
  button.removeEventListener('focusin', preventFocusIn);
};

export const hideOnPopperBlur: Plugin<Props> = {
  name: 'hideOnPopperBlur',
  defaultValue: true,
  fn(instance) {
    const { popper, hide, props } = instance;

    return {
      onCreate() {
        // We use this plugin to make sure that when focus leaves either the trigger button or the inner tooltip content, the tooltip closes
        popper.addEventListener('focusout', ({ relatedTarget }) => {
          if (
            props.hideOnPopperBlur &&
            relatedTarget &&
            !popper.contains(relatedTarget as Node)
          ) {
            hide();
          }
        });
      },
    };
  },
};

export const hideOnEsc: Plugin<Props> = {
  name: 'hideOnEsc',
  defaultValue: true,
  fn(instance) {
    const { props } = instance;

    function onKeyDown(event: KeyboardEvent) {
      if (props.hideOnEsc && event.key === 'Escape') {
        closePopperWithoutTriggeringFocusIn(instance);
      }
    }

    return {
      onShow() {
        document.addEventListener('keydown', onKeyDown);
      },
      onHide() {
        document.removeEventListener('keydown', onKeyDown);
      },
    };
  },
};

export const manageFocusInPopover: Plugin<Props> = {
  name: 'manageFocusInPopover',
  defaultValue: false,
  fn(instance) {
    const { popper, hide, props } = instance;

    let activeIndex = 0;
    let didAddListeners = false;

    const getFirstFocusableElement = () =>
      popper.querySelector<HTMLAnchorElement | HTMLButtonElement>('a, button');

    const moveFocusToFirstElementOnTab = (event: KeyboardEvent) => {
      if (event.key !== 'Tab' || event.shiftKey) {
        return;
      }

      event.preventDefault();

      const firstFocusableElement = getFirstFocusableElement();
      const triggerButton = getTriggerButton(instance.reference);

      if (firstFocusableElement && triggerButton) {
        firstFocusableElement.focus();
        triggerButton.removeEventListener(
          'keydown',
          moveFocusToFirstElementOnTab
        );
      }
    };

    const onKeydown = (event: KeyboardEvent) => {
      const focusableElements = popper.querySelectorAll<
        HTMLAnchorElement | HTMLButtonElement
      >('a, button');

      const numItems = focusableElements.length;
      if (
        !['Tab', ' ', 'Enter'].includes(event.key) ||
        !document.activeElement
      ) {
        return;
      }

      event.preventDefault();

      let newActiveIndex = activeIndex;
      switch (event.key) {
        case 'Tab':
          if (event.shiftKey) {
            newActiveIndex = activeIndex - 1;
          } else {
            newActiveIndex = activeIndex + 1;
          }
          break;
        case ' ':
        case 'Enter':
          focusableElements.item(activeIndex).click();
          hide();
          return;
      }

      if (newActiveIndex < 0 || newActiveIndex >= numItems) {
        // User has tabbed away from the popper, close the popper

        // focusin may be a trigger for the popper to show again, so we need to prevent it
        // from firing (and reopening the tooltip) when the user tabs back to the trigger button
        closePopperWithoutTriggeringFocusIn(instance);

        return;
      }

      activeIndex = newActiveIndex;
      focusableElements.item(newActiveIndex).focus();
    };
    return {
      onMount() {
        if (!props.manageFocusInPopover) return;

        activeIndex = 0;
        const firstFocusableElement = getFirstFocusableElement();
        const triggerButton = getTriggerButton(instance.reference);

        if (firstFocusableElement && triggerButton) {
          triggerButton.addEventListener(
            'keydown',
            moveFocusToFirstElementOnTab
          );
          popper.addEventListener('keydown', onKeydown);
          didAddListeners = true;
        }
      },
      onHidden() {
        if (!props.manageFocusInPopover) return;

        const triggerButton = getTriggerButton(instance.reference);

        activeIndex = 0;
        if (didAddListeners) {
          popper.removeEventListener('keydown', onKeydown);
          didAddListeners = false;
          triggerButton?.removeEventListener(
            'keydown',
            moveFocusToFirstElementOnTab
          );
        }
      },
    };
  },
};
