import {
  type ComponentPropsWithoutRef,
  forwardRef,
  type ForwardRefRenderFunction,
  type ReactNode,
} from 'react';
import React from 'react';

import type { LocationDescriptor } from '../types';
import { isNetlifyUrl } from './helpers';

export interface HTMLLinkProps extends ComponentPropsWithoutRef<'a'> {
  RouterLink:
    | typeof React.Component
    | React.ComponentType<{ to: LocationDescriptor; activeClassName?: string }>;
  to?: string;
  href?: string;
  disabled?: boolean;
  children?: ReactNode;
  className?: string;
  activeClassName?: string | undefined;
}

const HTMLLinkBase: ForwardRefRenderFunction<
  HTMLAnchorElement,
  HTMLLinkProps
> = (
  {
    to,
    href,
    disabled,
    children,
    className,
    activeClassName,
    RouterLink,
    ...rest
  },
  ref
) => {
  const link = to || href || '#';
  const isAnchor = link?.charAt?.(0) === '#';
  const isExternal = href && !isAnchor;
  const isNetlifyHref = href && isNetlifyUrl(href);

  const withRef = {
    ...((typeof ref === 'function' || ref?.hasOwnProperty('current')) && {
      /*
       ** ReactRouterLink needs innerRef
       ** adapt when upgrading react-router
       */
      [href ? 'ref' : 'innerRef']: ref,
    }),
  };

  if (disabled) {
    // set default role="link" on anchor without "href" (https://w3c.github.io/html-aria/#el-a-no-href) to prevent "clickable" announcement
    const { role = 'link' } = rest;

    return (
      <a role={role} aria-disabled="true" className={className} {...withRef}>
        {children}
      </a>
    );
  }

  if (isExternal) {
    const { target = '_blank', ...restWithoutTarget } = rest;
    return (
      <a
        className={className}
        target={target}
        {...(target === '_blank' && {
          rel: isNetlifyHref ? 'noopener' : 'noopener noreferrer',
          'aria-describedby': 'open-in-new-tab',
        })}
        href={href}
        {...restWithoutTarget}
        {...withRef}
      >
        {children}
      </a>
    );
  }

  if (isAnchor) {
    return (
      <a href={link} className={className} {...rest} {...withRef}>
        {children}
      </a>
    );
  }

  if (RouterLink) {
    return (
      <RouterLink
        to={link}
        className={className}
        activeClassName={activeClassName}
        {...rest}
        {...withRef}
      >
        {children}
      </RouterLink>
    );
  }

  console.warn(
    `The RouterLink prop of HTMLLink is not defined. Check the return of the renderLink at LinkProvider`
  );
  return null;
};

export const HTMLLink = forwardRef<HTMLAnchorElement, HTMLLinkProps>(
  HTMLLinkBase
);
