import * as React from 'react';

import { cn } from '../helpers/cn';
import type { DecorativeIconProps } from '../Icon/DecorativeIcon';
import { DecorativeIcon } from '../Icon/DecorativeIcon';
import type { IconName } from '../Icon/types';
import { Link } from '../Link/Link';

export enum BadgeState {
  Error = 'error',
  Info = 'info',
  Success = 'success',
  Warning = 'warning',
}

export enum BadgeVariant {
  Default = 'default',
  Highlight = 'highlight',
  Pop = 'pop',
  Upgrade = 'upgrade',
  Unlisted = 'unlisted',
}

type BadgeBaseProps<T extends React.ElementType> = {
  iconName?: IconName;
  iconProps?: Omit<DecorativeIconProps, 'name'>;
  disabled?: boolean;
  lowercase?: boolean;
  as?: T;
  title?: string;
  href?: string;
  className?: string;
  outset?: string;
  children?: React.ReactNode;
};

interface BadgeWithStateProps<T extends React.ElementType>
  extends BadgeBaseProps<T> {
  state?: BadgeState;
  variant?: never;
}

interface BadgeWithVariantProps<T extends React.ElementType>
  extends BadgeBaseProps<T> {
  state?: never;
  variant?: BadgeVariant;
}

export type BadgeProps<T extends React.ElementType = 'span'> = (
  | BadgeWithStateProps<T>
  | BadgeWithVariantProps<T>
) &
  Omit<React.ComponentPropsWithoutRef<T>, keyof BadgeBaseProps<T>>;

const stateCN = (state?: BadgeState) => {
  const classNames = {
    [BadgeState.Error]: `
      tw-bg-red-lighter
      tw-text-red-darkest
      dark:tw-bg-red-darkest
      dark:tw-text-red-lighter
    `,
    [BadgeState.Info]: `
      tw-bg-gray-lighter
      tw-text-black
      dark:tw-bg-gray
      dark:tw-text-white
    `,
    [BadgeState.Success]: `
      tw-bg-green-lighter
      tw-text-green-darkest
      dark:tw-bg-green-darkest
      dark:tw-text-green-lighter
    `,
    [BadgeState.Warning]: `
      tw-bg-gold-lighter
      tw-text-gold-darkest
      dark:tw-bg-gold-darkest
      dark:tw-text-gold-lighter
    `,
  };

  if (!state || !classNames[state]) {
    return null;
  }

  return cn('tw-font-medium', classNames[state]);
};

const variantCN = (variant?: BadgeVariant) => {
  const classNames = {
    [BadgeVariant.Default]: `
      tw-bg-gray-lighter
      tw-font-medium
      tw-text-black
      dark:tw-bg-gray
      dark:tw-text-white
    `,
    [BadgeVariant.Highlight]: `
      tw-bg-teal-lighter
      tw-font-semibold
      tw-text-teal-darkest
      dark:tw-bg-teal-lighter
      dark:tw-text-teal-darkest
    `,
    [BadgeVariant.Pop]: `
      tw-bg-gold
      tw-font-semibold
      tw-text-black
      dark:tw-text-gold-darkest
    `,
    [BadgeVariant.Upgrade]: `
      tw-bg-blue-lightest
      tw-font-semibold
      tw-text-black
      dark:tw-text-blue-darkest
    `,
    [BadgeVariant.Unlisted]: `
      tw-bg-gold-lightest
      tw-font-medium
      tw-text-gold-darkest
      dark:tw-bg-gold-darkest
      dark:tw-text-gold-lighter
    `,
  };

  if (!variant || !classNames[variant]) {
    return classNames[BadgeVariant.Default];
  }

  return classNames[variant];
};

export const Badge = React.forwardRef(
  <T extends React.ElementType = 'span'>(
    props: BadgeProps<T>,
    ref: React.ComponentPropsWithRef<T>['ref']
  ) => {
    const {
      as = 'span',
      state,
      variant,
      iconName,
      iconProps,
      disabled,
      lowercase,
      outset,
      className,
      href,
      title,
      children,
      ...rest
    } = props;

    if (!children && !iconName) {
      return null;
    }

    const Component = href ? Link : as;

    const badgeCN = cn(
      'tw-align-baseline',
      !lowercase && 'tw-capitalize',
      'tw-duration-200',
      'tw-ease-out',
      'tw-shrink-0 tw-grow-0 tw-basis-auto',
      'tw-inline-block',
      'tw-leading-4',
      'tw-px-1',
      'tw-py-0.5',
      'tw-relative',
      'tw-rounded-[4px]',
      'tw-self-center',
      'tw-text-sm',
      'tw-transition',
      'tw--top-px',
      'tw-whitespace-nowrap',
      stateCN(state) || variantCN(variant),
      disabled && 'tw-pointer-events-none tw-opacity-[0.7]',
      title && !href && 'tw-cursor-help',
      href && 'hover:tw-underline',
      outset,
      className
    );

    return (
      <Component
        {...rest}
        className={badgeCN}
        to={href}
        title={title}
        ref={ref}
      >
        {iconName && (
          <DecorativeIcon
            {...iconProps}
            name={iconName}
            className={cn('tw-mr-0.5 tw-align-bottom', iconProps?.className)}
            focusable="false"
          />
        )}
        {children}
      </Component>
    );
  }
) as <T extends React.ElementType = 'span'>(
  props: BadgeProps<T> & { ref?: React.ComponentPropsWithRef<T>['ref'] }
) => React.ReactElement | null;
