import type { ForwardRefRenderFunction, HTMLProps } from 'react';
import { forwardRef } from 'react';
import * as React from 'react';

import { cn } from '../helpers/cn';
import { DecorativeIcon } from '../Icon/DecorativeIcon';
import { useLink } from '../LinkProvider';
import type { LocationDescriptor } from '../types';

const INTENT_STYLES = {
  default: `
    tw-text-teal-darkest
    tw-cursor-pointer
    tw-font-semibold
    tw-relative
    hover:tw-underline
    dark:tw-text-teal-lighter
  `,
  invertUnderline: `hover:!tw-no-underline !tw-underline`,
  action: `
    tw-bg-none
    tw-border-0
    tw-text-teal-darkest
    tw-inline-block
    tw-font-semibold
    tw-pr-6
    tw-mt-2
    tw-rounded-sm
    focus-visible:tw-outline
    focus-visible:tw-outline-1
    hover:tw-underline
    after:tw-content-arrow
    after:tw-ml-1
    after:tw-absolute
    dark:tw-text-teal-lighter
  `,
  actionExternal: `
    after:tw-transform
    after:tw-rotate-[-40deg]
  `,
  showExternalIcon: `
    tw-inline-block
    tw-pr-6
    after:tw-content-arrow
    after:tw-ml-1
    after:tw-absolute
    after:tw-transform
    after:tw-rotate-[-40deg]
  `,
  anchor: `
    tw-text-inherit
    tw-font-weight-inherit
    tw-relative
    tw-outline-0
    tw-py-2
    tw-px-0
    tw--my-2
    tw-mx-0
    tw-no-underline
    focus:tw-no-underline
    hover:tw-no-underline
    after:tw-bg-gray
    after:tw-bottom-0
    after:tw-content-empty
    after:tw-mt-0.5
    after:tw-mask-image-icon-link
    after:tw-mask-center
    after:tw-mask-no-repeat
    after:tw-mask-4
    after:tw-w-6
    after:tw-top-0
    after:tw-absolute
    after:tw-opacity-0
    hover:after:tw-opacity-100
    focus:after:tw-opacity-100
    dark:tw-text-inherit
  `,
  block: `
    tw-text-inherit
    tw-font-weight-inherit
    tw-static
    tw-no-underline
    before:tw-absolute
    before:tw-top-0
    before:tw-right-0
    before:tw-bottom-0
    before:tw-left-0
    before:tw-content-empty
    before:tw-block
    before:z-0
    hover:tw-text-inherit
    hover:tw-underline
    dark:tw-text-inherit
  `,
  btnReset: `
    tw-border-0
    tw-bg-transparent
    tw-inline
    tw-p-0
  `,
  loud: `
    tw-text-black
    tw-font-semibold
    tw-underline
    tw-decoration-color-gray-light
    hover:tw-underline
    dark:tw-text-white
    dark:tw-decoration-color-gray-light
  `,
  subdued: ({
    lightModeInvert,
    underline = true,
  }: {
    lightModeInvert: boolean;
    underline: boolean;
  }) => `
    tw-text-inherit
    ${underline && 'tw-underline'}
    tw-decoration-gray
    tw-font-weight-inherit
    ${lightModeInvert ? `hover:tw-text-white` : `hover:tw-text-black`}
    hover:tw-decoration-inherit
    dark:hover:tw-text-white
    dark:tw-text-inherit
    dark:tw-decoration-inherit
  `,
  upgrade: `
    tw-inline-flex
    tw-items-center
    tw-font-medium
    tw-text-blue
    tw-text-sm
    tw-underline
    tw-whitespace-nowrap
    dark:tw-text-blue-lightest
  `,
};

export const linkCN = ({
  action,
  block,
  className,
  loud,
  subdued,
  subduedUnderline = true,
  upgrade,
  anchor,
  external, // To style Link with HTMLButton as external
  showExternalIcon,
  to,
  outset,
  href,
  lightModeInvert = false,
  invertUnderline = false,
}: Omit<LinkProps, 'children'>) => {
  const defaultLink =
    !className && !loud && !action && !block && !loud && !subdued && !anchor;
  // TODO: once all variants are sussed out, disallow className again
  return cn(
    defaultLink && INTENT_STYLES.default,
    action && INTENT_STYLES.action,
    showExternalIcon && INTENT_STYLES.showExternalIcon,
    (href || external) && action && INTENT_STYLES.actionExternal,
    block && INTENT_STYLES.block,
    loud && INTENT_STYLES.loud,
    subdued &&
      INTENT_STYLES.subdued({
        lightModeInvert,
        underline: subduedUnderline,
      }),
    upgrade && INTENT_STYLES.upgrade,
    anchor && INTENT_STYLES.anchor,
    !href && !to && INTENT_STYLES.btnReset,
    outset,
    invertUnderline && INTENT_STYLES.invertUnderline,
    className
  );
};

export interface LinkProps
  extends Omit<HTMLProps<HTMLAnchorElement | HTMLButtonElement>, 'action'> {
  action?: boolean;
  block?: boolean;
  loud?: boolean;
  subdued?: boolean;
  subduedUnderline?: boolean;
  invertUnderline?: boolean;
  upgrade?: boolean;
  anchor?: boolean;
  external?: boolean;
  showExternalIcon?: boolean;
  to?: LocationDescriptor | string;
  outset?: string;
  children: React.ReactNode;
  lightModeInvert?: boolean;
  type?: 'button' | 'submit' | 'reset';
}

const LinkBase: ForwardRefRenderFunction<
  HTMLAnchorElement | HTMLButtonElement,
  LinkProps
> = (
  {
    action,
    block,
    className,
    loud,
    subdued,
    subduedUnderline,
    upgrade,
    anchor,
    external,
    showExternalIcon,
    type = 'button',
    outset = '', // do not use this prop directly; outset should only be passed if the <Link> instance is wrapped with <Outset> component
    children,
    lightModeInvert = false,
    invertUnderline,
    ...rest
  },
  ref
) => {
  const renderLink = useLink();
  const { to, href, onClick } = rest;
  const CN = linkCN({
    action,
    block,
    className,
    loud,
    subdued,
    subduedUnderline,
    showExternalIcon,
    upgrade,
    anchor,
    external,
    outset,
    lightModeInvert,
    to,
    href,
    invertUnderline,
  });
  return to || href || !onClick ? (
    renderLink({
      ref: ref as React.ForwardedRef<HTMLAnchorElement>,
      className: CN,
      ...(rest as React.ComponentPropsWithoutRef<'a'>),
      children: (
        <>
          {upgrade && <DecorativeIcon name="locked" />}
          {children}
        </>
      ),
    })
  ) : (
    <button
      className={cn('tw-text-left', CN)}
      type={type}
      {...(rest as React.ComponentPropsWithoutRef<'button'>)}
    >
      {children}
    </button>
  );
};

export const Link = forwardRef<
  HTMLAnchorElement | HTMLButtonElement,
  LinkProps
>(LinkBase);
