import type { DownshiftProps } from 'downshift';
import Downshift from 'downshift';
import type { ReactNode } from 'react';

import { Button } from '../Button/Button';
import type { ButtonLevel } from '../Button/types';
import { cn } from '../helpers/cn';
import { billingPath, docsPath, forumsPath } from '../helpers/path';
import { useHistory } from '../HistoryProvider';
import { DecorativeIcon } from '../Icon/DecorativeIcon';
import type { IconName } from '../Icon/types';
import { isNetlifyUrl } from '../Link/helpers';
import {
  asideCN,
  badgeCN,
  dropdownWrapperCN,
  externalLinkCN,
  iconCN,
  listItemCN,
  listItemHeadingCN,
  listItemLinkCN,
  metaCN,
  notificationsButtonCN,
} from './styles';

const AVAILABLE = 'Available';
const NO_CTA = 'No Call To Action';
const UPGRADE_CTA = 'UpgradeButton';

interface Props<Item = string> extends DownshiftProps<Item> {
  userEmail?: string;
  align?: string;
  onClickUpgrade?: () => void;
  onClickUpgradeForHighSupportPriority?: () => void;
  accountSlug?: string;
  accountSupportLevel?: number;
  accountSupportPriority?: number;
  showUpgrade?: boolean;
  pricingUrl?: string;
  buttonLevel?: ButtonLevel;
}

type SupportDropdownItem = {
  title: string;
  iconName?: IconName;
  href?: string;
  primary?: boolean;
  description: ReactNode;
  children?: ReactNode;
  watermark?: 'support' | 'email';
  badge?: string;
};

export function SupportDropdown({
  userEmail,
  align = 'left',
  onClickUpgrade,
  onClickUpgradeForHighSupportPriority,
  isOpen,
  accountSlug,
  accountSupportLevel = 0,
  accountSupportPriority,
  showUpgrade = showUpgradeCallToAction(accountSupportLevel),
  pricingUrl,
  buttonLevel = 'tertiary',
}: Props) {
  const browserHistory = useHistory();

  const accountIsHighSupportPriority = accountSupportPriority
    ? accountSupportPriority >= 5
    : false;

  const redirectToPricingPage = () => {
    if (pricingUrl) {
      const isNetlifyLink = isNetlifyUrl(pricingUrl);
      const options = isNetlifyLink ? 'noopener' : 'noopener, noreferrer';
      window.open(pricingUrl, '_blank', options);
      return;
    }

    if (accountSlug) {
      browserHistory.push(billingPath({ slug: accountSlug }, 'pricing'));
    }
  };

  const handleSelect = (selectedItem: string | null) => {
    if (selectedItem === UPGRADE_CTA) {
      onClickUpgrade?.();

      if (accountIsHighSupportPriority) {
        onClickUpgradeForHighSupportPriority?.();
      } else {
        redirectToPricingPage();
      }
    } else if (selectedItem && selectedItem !== NO_CTA) {
      window.open(selectedItem, '_blank');
    }
  };

  return (
    <Downshift onSelect={handleSelect} isOpen={isOpen}>
      {({
        getToggleButtonProps,
        getLabelProps,
        getItemProps,
        isOpen,
        closeMenu,
        highlightedIndex,
        toggleMenu,
      }) => {
        const items: SupportDropdownItem[] = [
          {
            title: 'Ask Netlify',
            iconName: 'ask-netlify',
            href: getAskNetlifyUrl(),
            primary: true,
            description:
              'Our generative AI chatbot assistant can quickly answer your questions about Netlify.',
            children: 'Ask us anything',
          },
          {
            title: 'Forums',
            iconName: 'user',
            href: forumsPath(),
            description:
              'Ask questions and find answers from Netlify Support staff and other community members.',
            children: 'Visit Netlify Forums',
          },
          {
            title: 'Docs',
            iconName: 'file',
            href: docsPath(),
            description:
              'Learn how to make the most of your Netlify sites with detailed instructions, tips, and reference materials.',
            children: 'Visit our docs',
          },
        ];

        if (accountIsHighSupportPriority) {
          items.push({
            title: 'Email',
            iconName: 'email',
            href: getSupportUrl(userEmail),
            description: getSupportDescription(accountSupportLevel),
            children: 'Email support',
          });
        }

        if (showUpgrade) {
          items.push({
            title: accountIsHighSupportPriority
              ? 'Enterprise Support'
              : 'Email',
            href: UPGRADE_CTA,
            watermark: accountIsHighSupportPriority ? 'support' : 'email',
            description: getUpgradeDescription(
              accountIsHighSupportPriority && showUpgrade,
              accountSupportLevel
            ),
            children: <Button variant="pop">Upgrade</Button>,
          });
        } else {
          items.push(
            {
              title: 'Slack',
              iconName: 'slack',
              badge: AVAILABLE,
              description: (
                <>
                  <strong className="tw-font-semibold">
                    Visit your dedicated Slack channel
                  </strong>{' '}
                  to chat with your account team about implementation, best
                  practices, go-live details, and broader support queries.
                </>
              ),
            },
            {
              title: 'Emergency phone support',
              iconName: 'phone',
              badge: AVAILABLE,
              description: (
                <>
                  For critical downtime emergencies, you can{' '}
                  <strong className="tw-font-semibold">
                    call us at your team’s dedicated phone number
                  </strong>
                  .
                </>
              ),
            }
          );
        }

        return (
          <div className={dropdownWrapperCN}>
            <Button
              {...getToggleButtonProps({
                'aria-label': `Support. ${isOpen ? 'Close menu' : 'Open menu'}`,
              })}
              {...getLabelProps()}
              level={buttonLevel}
              className={notificationsButtonCN({ isOpen })}
              onClick={() => toggleMenu()}
            >
              {/* Hide and reveal icons with CSS rather than swapping out SVG to avoid layout flashes as link becomes active and icon loads for first time */}
              <DecorativeIcon
                className={cn('tw-shrink-0', 'tw-mr-3', isOpen && 'tw-hidden')}
                focusable="false"
                name="nav-support"
              />
              <DecorativeIcon
                className={cn('tw-shrink-0', 'tw-mr-3', !isOpen && 'tw-hidden')}
                focusable="false"
                name="nav-support-active"
              />
              <span
                className="layout-toggleable-bold-text"
                data-text-content="Support"
              >
                Support
              </span>
            </Button>
            {isOpen && (
              <aside
                className={cn(asideCN, {
                  'tw-left-auto tw-right-0': align === 'right',
                })}
              >
                <ul className="tw-h-full tw-max-h-notifications-list tw-overflow-auto lg-height:tw-max-h-170">
                  {items.map(
                    (
                      {
                        title,
                        iconName,
                        href,
                        primary = false,
                        watermark,
                        badge,
                        description,
                        children,
                      },
                      index
                    ) => {
                      const isHighlighted = index === highlightedIndex;
                      const Component = href ? 'a' : 'div';
                      const isExternalLink =
                        href && typeof children === 'string';

                      return (
                        <li
                          key={title}
                          className={listItemCN({ watermark, primary })}
                        >
                          <Component
                            {...getItemProps({ index, item: href || NO_CTA })}
                            tabIndex={-1}
                            className={listItemLinkCN({
                              isHighlighted,
                              watermark,
                              primary,
                            })}
                          >
                            <span className="tw-sr-only">Support: {title}</span>
                            {iconName && (
                              <DecorativeIcon
                                name={iconName as IconName}
                                className={iconCN({ primary })}
                              />
                            )}
                            {/* Note: wrapper div exists to allow for flexbox layout */}
                            <div>
                              <div className="tw-flex tw-items-start tw-justify-between">
                                <p
                                  className={listItemHeadingCN({
                                    watermark,
                                  })}
                                >
                                  <span className="tw-not-sr-only">
                                    {title}
                                  </span>
                                  {badge && (
                                    <span className={badgeCN}>{badge}</span>
                                  )}
                                </p>
                              </div>
                              <p className={metaCN({ watermark })}>
                                {description}
                              </p>
                              {/* Note: using <p> bc the actual <ConditionalWrapper> wraps this whole block */}
                              {children && (
                                <p
                                  className={cn(
                                    'tw-mt-2',
                                    isExternalLink
                                      ? externalLinkCN({ isHighlighted })
                                      : undefined
                                  )}
                                >
                                  {children}
                                </p>
                              )}
                            </div>
                          </Component>
                        </li>
                      );
                    }
                  )}
                </ul>
              </aside>
            )}
          </div>
        );
      }}
    </Downshift>
  );
}

function getAskNetlifyUrl(message?: string) {
  const url = new URL(docsPath('ask-netlify'));
  const params = new URLSearchParams({ attr: 'app' });

  if (message) params.set('message', message);

  url.search = params.toString();

  return url.href;
}

function getSupportDescription(level: number) {
  if (level < 1) {
    return 'Access support by email.';
  }

  return (
    <>
      Private support with guaranteed response times.
      <br />
      {level === 1 ? '(Business days, 6 AM — 5 PM PST/PDT)' : '24x7x365'}
      <br />
      Business critical: 1 hour
      <br />
      General issue: 4 hours
    </>
  );
}

function getSupportUrl(userEmail?: string) {
  const url = new URL('/support', 'https://www.netlify.com');
  const params = new URLSearchParams({
    utm_source: 'app-dropdown',
    utm_medium: 'app',
    utm_campaign: 'support-insights',
  });

  if (userEmail) params.set('email', userEmail);

  url.search = params.toString();

  return url.href;
}

function getUpgradeDescription(upgrade: boolean, level: number) {
  if (upgrade) {
    return 'Upgrade to get guaranteed response times and dedicated emergency support.';
  }
  if (level === 1) {
    return 'Upgrade to get a dedicated account team and emergency phone support.';
  }
  return 'Upgrade to a paid plan to privately email support.';
}

export function showUpgradeCallToAction(accountSupportLevel: number) {
  return accountSupportLevel < 2;
}
