import lodashSplit from 'lodash/split';
import {
  type ElementRef,
  type ReactNode,
  useEffect,
  useRef,
  useState,
} from 'react';

import { cn } from '../helpers/cn';
import { getPlaceholderGradientColorsFromId } from '../helpers/placeholderColors';
import { Picture } from '../Picture/Picture';
import { Tooltip } from '../Tooltip/Tooltip';
import { getAvatarSrcFromUser, isAvatarUrlCustomUpload } from './helpers';

const GRAVATAR_URL = 'https://secure.gravatar.com/avatar';

/* These sizes are applied directly on img tag, on width and height attributes
 * Any new size will also need a new css class!
 */
const sizes = {
  xs: 16,
  sm: 24,
  md: 40,
  lg: 100,
  xl: 128,
};

function transformAvatarUrl(
  url: string,
  size: number,
  scale: number = 2
): string {
  const scaledSize = Math.round(size * scale);
  if (url.startsWith(GRAVATAR_URL)) {
    return `${url.split('?')[0]}?s=${scaledSize}&d=404`;
  }
  if (url.match(/github/i)) {
    return `${url.split('?')[0]}?s=${scaledSize}`;
  }
  if (url.match(/gitlab/i)) {
    return `${url.split('?')[0]}?size=${scaledSize}`;
  }
  return url;
}

const AvatarWrapper = ({ children, hoverTooltip }) => {
  if (hoverTooltip) {
    return (
      <Tooltip
        description={hoverTooltip}
        trigger="mouseenter"
        placement="bottom"
        className="!tw-p-2 !tw-py-1"
        appendTo={document.body}
      >
        {children}
      </Tooltip>
    );
  }
  return children;
};

export type AvatarUser = {
  id?: string;
  avatar_url?: string;
  full_name?: string;
  email?: string;
  avatar?: string | { href?: string };
  user_metadata?: { avatar?: string; full_name?: string };
};

export interface AvatarProps {
  user?: AvatarUser;
  size?: 'xs' | 'sm' | 'md' | 'lg' | 'xl';
  alt?: boolean;
  altText?: string;
  src?: string | null;
  showInnerRing?: boolean;
  dropdown?: boolean;
  className?: string;
  title?: string;
  placeholderText?: string;
  placeholderBackgroundColors?: { to: string; from: string };
  hoverTooltip?: string | ReactNode;
}

export function Avatar({
  user,
  size,
  alt,
  altText,
  src,
  showInnerRing = true,
  dropdown = false,
  className = '',
  title,
  placeholderText,
  placeholderBackgroundColors,
  hoverTooltip,
}: AvatarProps) {
  const [isErrored, setIsErrored] = useState(false);
  const [isLoaded, setIsLoaded] = useState(false);

  const imgRef = useRef<ElementRef<'img'>>(null);

  src = src || getAvatarSrcFromUser(user);

  const px = (size && sizes[size]) || 32;
  const transformedSrc = src ? transformAvatarUrl(src, px) : null;

  useEffect(() => {
    // If the src prop changes, reset state to defaults
    setIsErrored(false);

    const img = imgRef.current;
    const isLoaded = img !== null && img.src === transformedSrc && img.complete;

    setIsLoaded(isLoaded);
  }, [transformedSrc]);

  const username =
    (user && (user.full_name || user.user_metadata?.full_name || user.email)) ||
    'Anonymous';
  const classNames = cn(
    'tw-rounded-full',
    'tw-block',
    'tw-self-center',
    'tw-overflow-hidden',
    'tw-aspect-square',
    // Semitransparent circle around the avatar
    showInnerRing &&
      `after:tw-absolute after:tw-inset-0 after:tw-select-none after:tw-rounded-full after:tw-border after:tw-border-black/10 after:tw-content-[''] dark:after:tw-border-white/25`,
    !size &&
      !dropdown &&
      `tw-w-avatar-sm tw-text-lg tw-leading-[theme('height.avatar-sm')]`,
    size === 'xs' && `tw-w-4 tw-text-[10px] tw-leading-4`,
    size === 'sm' && `tw-w-6 tw-text-[14px] tw-leading-6`,
    size === 'md' && `tw-w-10 tw-text-[24px] tw-leading-10`,
    size === 'lg' &&
      `tw-w-avatar-lg tw-text-[40px] tw-leading-[theme('height.avatar-lg')] md:tw-w-avatar-xl md:tw-text-[60px] md:tw-leading-[96px]`,
    size === 'xl' &&
      `tw-w-avatar-xl tw-text-[60px] tw-leading-[96px] md:tw-w-avatar-xxl md:tw-text-[72px] md:tw-leading-[124px]`,
    dropdown
      ? `tw-absolute tw-inset-0 tw-w-avatar-md tw-text-xl tw-leading-[theme('height.avatar-md')]`
      : 'tw-relative',
    className
  );

  const textClassNames = cn(
    'tw-font-semibold',
    'tw-text-center',
    'tw-text-white'
  );
  const altInfo = altText || username;

  // Assume we shouldn't show the title if we're showing a tooltip
  const formattedTitle = hoverTooltip
    ? undefined
    : title || altText || (username !== 'Anonymous' && username) || undefined;

  if (src && !isErrored) {
    const customAvatar = isAvatarUrlCustomUpload(src);
    const formattedAlt = alt ? altInfo : '';

    return (
      <AvatarWrapper hoverTooltip={hoverTooltip}>
        <span
          className={classNames}
          title={formattedTitle}
          data-chromatic="ignore"
        >
          {customAvatar ? (
            <Picture
              className="tw-aspect-square tw-w-full"
              url={src}
              width={px}
              height={px}
              alt={formattedAlt}
              onError={() => setIsErrored(true)}
              cover
              animated={`${src}`.endsWith('.gif')}
            />
          ) : (
            <img
              ref={imgRef}
              className={cn(
                'tw-aspect-square tw-w-full',
                !isLoaded && 'tw-opacity-0'
              )}
              src={transformAvatarUrl(src, px)}
              width={px}
              height={px}
              alt={formattedAlt}
              onError={() => setIsErrored(true)}
              onLoad={() => setIsLoaded(true)}
              loading="lazy"
            />
          )}
        </span>
      </AvatarWrapper>
    );
  }

  const placeholder =
    placeholderText ||
    (username && lodashSplit(username, '', 1)[0].toUpperCase()); // lodash allows for fancy emojis as the first letter of the username
  const backgroundColors =
    placeholderBackgroundColors || getPlaceholderGradientColorsFromId(user?.id);

  return (
    <AvatarWrapper hoverTooltip={hoverTooltip}>
      <span
        className={cn(classNames, textClassNames)}
        title={formattedTitle}
        aria-hidden={!alt}
        style={{
          background: `linear-gradient(to bottom right, ${backgroundColors.to}, ${backgroundColors.from})`,
        }}
      >
        {placeholder}
        {alt && <span className="tw-sr-only">{altInfo}</span>}
      </span>
    </AvatarWrapper>
  );
}
