export const isNetlifyUrl = (url: string) => {
  const isAnchor = url.charAt?.(0) === '#';
  const isExternal = url && !isAnchor;
  let isNetlifyProperty = !isExternal;

  if (isExternal) {
    try {
      const { origin } = new URL(url);
      isNetlifyProperty =
        /\.netlify\.com$/.test(origin) || /\.stackbit\.com$/.test(origin);
    } catch {}
  }

  return isNetlifyProperty;
};
