const CLOUDFRONT_BUCKET_BASE_URL = 'https://d33wubrfki0l68.cloudfront.net/';
const SCREENSHOT_PROXY_BASE_URL = 'https://screenshot-proxy.netlify.app/';
const NETLIFY_IMAGE_CDN_BASE_URL = '/.netlify/images';

export type ImageFormat = 'avif' | 'gif' | 'jpg' | 'png' | 'webp';
type Fit = 'contain' | 'cover' | 'fill'; // default: contain
type Position = 'top' | 'bottom' | 'left' | 'right' | 'center'; // default: center

type ScreenshotProxyOptions = {
  animated?: boolean;
  cover?: boolean;
  fit?: Fit;
  format: ImageFormat;
  height?: number;
  scale?: number;
  width: number;
};

type ImageCDNOptions = {
  cover?: boolean; // exists for backwards-compat with ScreenshotProxyOptions
  fit?: Fit;
  format: ImageFormat; // ignored; exists for backwards-compat with ScreenshotProxyOptions
  height?: number;
  position?: Position;
  quality?: number; // default: 75
  scale?: number;
  width: number;
  url: string;
};

const screenshotProxyOptions = ({
  animated,
  cover,
  fit,
  format,
  height,
  scale = 2,
  width,
}: ScreenshotProxyOptions): string => {
  // Visit https://github.com/netlify/screenshot-proxy to see available options
  const formattedOptions = [`f_${format}`];

  if ((cover || fit === 'cover') && height) {
    formattedOptions.push('fit_cover');
    formattedOptions.push(
      `s_${Math.round(width * scale)}x${Math.round(height * scale)}`
    );
  } else {
    formattedOptions.push(`w_${Math.round(width * scale)}`);
  }

  if (animated) {
    formattedOptions.push('animated');
  }

  return formattedOptions.join(',');
};

const imageCDNOptions = ({
  fit,
  height,
  position,
  quality,
  scale = 2,
  width,
  url,
}: ImageCDNOptions): string => {
  // Visit https://github.com/netlify/screenshot-proxy to see available options

  // Note: `format` is intentionally omitted!
  // Netlify Image CDN automatically handles content negotiation
  // to use the most efficient image format for the requesting client

  return [
    `url=${encodeURI(url)}`,
    fit && `fit=${fit}`,
    height && `h=${height * scale}`,
    position && `position=${position}`,
    quality && `q=${quality}`,
    width && `w=${width * scale}`,
  ]
    .filter(Boolean)
    .join('&');
};

const shouldUseNetlifyImageCDN = (url: string = ''): boolean => {
  // TODO: Add back when we can use Image CDN
  return false;
  // return (
  //   typeof url !== 'string' ||
  //   url.startsWith('/') ||
  //   url.startsWith(CLOUDFRONT_BUCKET_BASE_URL)
  // );
};

export const generateImageUrl = (
  url: string = '',
  options: ScreenshotProxyOptions | ImageCDNOptions
): string => {
  // if already prefixed with the base url, simply return the url
  if (
    typeof url !== 'string' ||
    url.startsWith('blob:') ||
    url.startsWith(SCREENSHOT_PROXY_BASE_URL) ||
    url.startsWith(NETLIFY_IMAGE_CDN_BASE_URL)
  ) {
    return url;
  }

  // relative paths, and our special cloudfront bucket, are supported by Netlify Image CDN
  // see https://docs.netlify.com/image-cdn/overview/#remote-path
  if (shouldUseNetlifyImageCDN(url)) {
    const fit = options.cover ? 'cover' : undefined;
    return `${NETLIFY_IMAGE_CDN_BASE_URL}?${imageCDNOptions({ url, fit, ...options })}`;
  }

  // all other requests should go through the screenshot-proxy site
  return `${SCREENSHOT_PROXY_BASE_URL}${screenshotProxyOptions(options)}/${encodeURI(url)}`;
};

export const getSources = ({
  formats,
  url,
  options,
}): { srcSet: string; type: string }[] => {
  if (shouldUseNetlifyImageCDN(url)) {
    return [];
  }
  return formats.map((format: ImageFormat) => {
    return {
      srcSet: generateImageUrl(url, { ...options, format }),
      type: `image/${format}`,
    };
  });
};
