import type {
  DownshiftProps as IDownshiftProps,
  UseSelectStateChange,
} from 'downshift';
import Downshift from 'downshift';
import { useRef } from 'react';
import reactToText from 'react-to-text';

import { Button } from '../Button/Button';
import ButtonIcon from '../Button/ButtonIcon';
import type { ButtonLevel } from '../Button/types';
import { cn } from '../helpers/cn';
import { useHistory } from '../HistoryProvider';
import { AccessibleIcon } from '../Icon/AccessibleIcon';
import { DecorativeIcon } from '../Icon/DecorativeIcon';
import type { IconName } from '../Icon/types';
import { isNetlifyUrl } from '../Link/helpers';
import { Link } from '../Link/Link';
import { useDropdownAlignment } from './useDropdownAlignment';

const defaultItemToString = <ItemValue = string,>(
  item: IDropdownItem<ItemValue> | null
): string => {
  return reactToText(item?.label || '').trim();
};

const toggleToString = <ItemValue = string,>(
  toggle: Toggle | IDropdownItem<ItemValue> | null
): string => {
  return reactToText(toggle?.label || '').trim();
};

type ButtonConfigMapItem = {
  level: ButtonLevel;
  className?: string;
};

const buttonConfigMap: { [key: string]: ButtonConfigMapItem } = {
  default: {
    level: 'secondary',
  },
  bare: {
    level: 'tertiary',
  },
  'bare-large': {
    level: 'tertiary',
    className: cn(
      'tw-text-xl',
      'tw-font-semibold',
      '!tw-leading-none',
      'tw-w-auto',
      'tw-text-black',
      'dark:tw-text-white',
      'dark:hover:tw-bg-transparent'
    ),
  },
  primary: {
    level: 'primary' as ButtonLevel,
  },
};

const SelectedIcon = () => (
  <AccessibleIcon
    title="selected"
    aria-label="selected"
    name="checkmark"
    className="tw-absolute tw-right-4 tw-top-1/2 tw--translate-y-1/2"
    width={12}
  />
);

export interface IDropdownItem<Item = string> {
  [key: string]: unknown;
  label: string | React.ReactNode;
  value?: Item;
  className?: string | Record<string, boolean>;
  action?: (...args) => void;
  onClick?: () => void;
  selected?: boolean;
  active?: boolean;
  divider?: boolean;
  to?: string;
  href?: string;
  disabled?: boolean;
}

type DropdownItemGroup<ItemValue = string> = {
  label?: string | React.ReactNode;
  items: IDropdownItem<ItemValue>[];
  relativeIndex?: number;
};

type InferedDropdownProps<ItemValue = string> = {
  items?: IDropdownItem<ItemValue>[];
  itemGroups?: DropdownItemGroup<ItemValue>[];
  onStateChange?: (changes: UseSelectStateChange<string>) => void;
};

type Toggle = {
  label: string | React.ReactNode;
  ariaLabel?: string;
  activeClassName?: string;
  inactiveClassName?: string;
  className?: string;
  saving?: boolean;
};

interface IDropdownProps<ItemValue = string>
  extends Omit<IDownshiftProps<IDropdownItem<ItemValue>>, 'onSelect'> {
  alignRight?: boolean;
  positionMenuAbove?: boolean;
  className?: string;
  toggle?: Toggle;
  noCaret?: boolean;
  onClick?: () => void;
  onSelect?: (selectedItem: IDropdownItem<ItemValue> | null) => void;
  scrollable?: boolean;
  disabled?: boolean;
  variant?: keyof typeof buttonConfigMap;
  menu?: {
    className?: string;
  };
  caret?: {
    className?: string;
    cancelWidth?: boolean;
    iconName?: IconName;
    width?: number;
    height?: number;
  };
  pinnedItems?: IDropdownItem<ItemValue>[] | null;
  condensed?: boolean;
  noCheck?: boolean;
  iconButton?: IconName;
}

type ProviderProps =
  | { provider?: undefined; ButtonAuthProvider?: undefined }
  | {
      provider: string;
      ButtonAuthProvider: (props) => JSX.Element;
    };

export type DropdownProps<ItemValue = string> =
  InferedDropdownProps<ItemValue> & IDropdownProps<ItemValue> & ProviderProps;

export function Dropdown<ItemValue>({
  className,
  toggle,
  items = [],
  menu,
  noCaret,
  onClick,
  onSelect,
  scrollable = false,
  disabled = false,
  alignRight = false,
  positionMenuAbove = false,
  variant = 'default',
  provider,
  ButtonAuthProvider,
  caret,
  pinnedItems = [],
  condensed,
  itemGroups,
  noCheck,
  iconButton,
  itemToString = defaultItemToString,
  ...rest
}: DropdownProps<ItemValue>) {
  const dropdownContentRef = useRef<HTMLElement | null>(null);
  const browserHistory = useHistory();

  const align = useDropdownAlignment(
    dropdownContentRef,
    alignRight ? 'right' : 'left'
  );

  const handleSelect = (selectedItem, other) => {
    if (!selectedItem) {
      return;
    }

    const { to, href, reloadPage } = selectedItem;

    onSelect?.(selectedItem);

    // When triggered by the keyboard, links inside the dropdown have not been invoked directly since focus technically
    // remains on the dropdown button as per downshift behaviour.
    // Therefore, if the item includes a link, we make sure it's pushed onto the browser history
    if (href) {
      const isNetlifyLink = isNetlifyUrl(href);
      const options = isNetlifyLink ? 'noopener' : 'noopener, noreferrer';
      window.open(href, '_blank', options);
      return;
    }
    // The reload page prop is a workaround for site switcher state management issues,
    // making sure a full page reload happens on this route change
    if (to && reloadPage) {
      window.location.href = to;
      return;
    }
    if (to) {
      browserHistory.push(to);
    }
  };

  const buttonConfig = buttonConfigMap[variant];

  itemGroups?.forEach((itemGroup, index) => {
    if (index > 0) {
      const prevGroup = itemGroups[index - 1];
      itemGroup.relativeIndex =
        (prevGroup.relativeIndex ? prevGroup.relativeIndex : 0) +
        prevGroup.items.length;
    } else {
      itemGroup.relativeIndex = 0;
    }
  });

  const isItemSelected = (
    item: IDropdownItem<ItemValue>,
    selectedItem: IDropdownItem<ItemValue> | null
  ) => {
    if (item.selected) {
      return true;
    }
    const stringifiedItem = itemToString(item);
    if (stringifiedItem && selectedItem) {
      return stringifiedItem === itemToString(selectedItem);
    }
  };

  const checkIfExists = (obj, prop) =>
    typeof obj === 'object' && Boolean(obj[prop]);
  const applyIfExists = (obj, prop) =>
    checkIfExists(obj, prop) ? obj[prop] : ``;

  const countOfAllItems =
    itemGroups?.reduce((prevVal, group) => {
      const groupSize =
        applyIfExists(group.items, 'size') || group.items?.length || 0;
      return prevVal + groupSize;
    }, 0) ||
    applyIfExists(items, 'size') ||
    items?.length;

  return (
    <Downshift<IDropdownItem<ItemValue>>
      onSelect={handleSelect}
      itemToString={itemToString}
      {...rest}
    >
      {({
        getRootProps,
        getToggleButtonProps,
        getItemProps,
        isOpen,
        toggleMenu,
        selectedItem,
        highlightedIndex,
        getInputProps,
        getMenuProps,
      }) => {
        const hasMenu = toggle ? countOfAllItems > 0 : countOfAllItems > 1;
        const button =
          toggle || selectedItem || (items.length ? items[0] : undefined);

        const label = button?.label;

        if (!label) {
          return null;
        }

        const menuProps = getMenuProps(
          {
            'aria-labelledby': undefined,
          },
          { suppressRefError: true }
        );

        const rootProps = {
          ...getRootProps(
            /* NOTE: Override the default Downshift "role='combobox'" and "aria-labelledby" attribute,
              since we are using Downshift for dropdown-select style functionality only and not autocomplete or input.
              See https://github.com/downshift-js/downshift/issues/750#issuecomment-524650373
            */
            {
              'aria-labelledby': undefined,
              role: undefined,
            },
            /* NOTE: We suppress ref errors since we are applying to a div, not a custom component.
              See https://github.com/downshift-js/downshift/issues/731#issuecomment-729758697
            */
            { suppressRefError: true }
          ),
        };

        if (!hasMenu) {
          return toggle ? null : (
            <div className={className} {...rootProps}>
              {label}
            </div>
          );
        }

        const DropdownButton = provider
          ? ButtonAuthProvider
          : iconButton
            ? ButtonIcon
            : Button;

        const shouldUseGroupedList =
          (itemGroups && itemGroups?.length > 0) ||
          (pinnedItems && pinnedItems.length > 0);
        const MenuWrapper = shouldUseGroupedList ? 'ul' : 'div';

        const toggleProvidedAriaLabel =
          typeof toggle === 'object' && toggle.ariaLabel
            ? toggle.ariaLabel
            : '';

        return (
          <div
            className={cn('dropdown', className, {
              open: isOpen,
            })}
            {...rootProps}
          >
            <DropdownButton
              level={buttonConfig.level}
              {...getToggleButtonProps({
                'aria-label': `${toggleProvidedAriaLabel || toggleToString(button)}. ${
                  isOpen ? 'Close menu' : 'Open menu'
                }`,
                'aria-activedescendant':
                  getInputProps()['aria-activedescendant'],
              })}
              disabled={disabled}
              name={toggleToString(button)}
              onClick={() => {
                toggleMenu();
                // call onClick if present and menu is closed
                if (onClick && !isOpen) {
                  onClick();
                }
              }}
              className={
                provider
                  ? 'tw-w-full tw-bg-white dark:tw-bg-black'
                  : cn(
                      buttonConfig.className || '',
                      applyIfExists(button, 'className'),
                      {
                        [applyIfExists(button, `activeClassName`)]:
                          checkIfExists(button, 'active') || isOpen,
                        [applyIfExists(button, `inactiveClassName`)]:
                          !checkIfExists(button, 'active') && !isOpen,
                      }
                    )
              }
              provider={provider}
              condensed={condensed}
              saving={checkIfExists(button, 'saving')}
              title={toggleToString(button)}
              iconName={iconButton}
            >
              {label}
              {!noCaret && !checkIfExists(button, 'saving') && (
                <DecorativeIcon
                  name={caret?.iconName || 'caret-down'}
                  width={caret?.width || 12}
                  height={caret?.height || 12}
                  className={cn(
                    'tw-ml-2 tw-transition-transform tw-duration-100 tw-ease-cubic-bezier',
                    caret?.className,
                    isOpen && 'tw--rotate-180 tw-transform',
                    caret?.cancelWidth && 'tw--mr-5'
                  )}
                />
              )}
            </DropdownButton>
            <MenuWrapper
              className={cn('dropdown-inner dropdown-menu', menu?.className, {
                '!tw-left-auto !tw-right-0': align === 'right',
                'tw-bottom-[calc(100%+theme(spacing.2))] tw-top-auto':
                  positionMenuAbove,
              })}
              ref={dropdownContentRef}
              {...(shouldUseGroupedList && menuProps)}
            >
              <div
                className={cn({
                  'dropdown-menu--scrollable': scrollable,
                  'tw-overflow-x-hidden': scrollable,
                })}
              >
                {(itemGroups || [{ items }]).map((item, index) => {
                  const { label: groupLabel, items, relativeIndex } = item;
                  return (
                    <ul
                      aria-labelledby={
                        groupLabel ? `group-label-${index}` : undefined
                      }
                      key={`group-${index}`}
                      {...(!shouldUseGroupedList && menuProps)}
                    >
                      {groupLabel && (
                        <li
                          id={`group-label-${index}`}
                          role="presentation"
                          className={cn(
                            'menuitem !tw-text-sm !tw-font-regular !tw-text-gray-darker dark:!tw-text-gray-lighter',
                            { divider: index !== 0 }
                          )}
                        >
                          {groupLabel}
                        </li>
                      )}
                      {items.map((item, index) => {
                        const {
                          className,
                          selected,
                          active,
                          divider,
                          to,
                          href,
                          disabled,
                          onClick,
                        } = item;

                        const itemRelativeIndex = relativeIndex
                          ? relativeIndex + index
                          : index;

                        return (
                          <li key={index}>
                            <Link
                              {...getItemProps({
                                item,
                                index: itemRelativeIndex,
                                to,
                                href,
                                disabled,
                                ...(noCheck
                                  ? { 'aria-selected': undefined }
                                  : {}),
                                onClick: (e) => {
                                  // Prevent duplicate navigation when clicking on a link (handled in onSelect)
                                  e.preventDefault();
                                  onClick?.();
                                },
                              })}
                              className={cn('menuitem', className, {
                                highlighted:
                                  itemRelativeIndex === highlightedIndex,
                                selected: noCheck ? null : selected,
                                active:
                                  active || isItemSelected(item, selectedItem),
                                divider,
                                disabled,
                              })}
                            >
                              {item.label}
                              {!noCheck &&
                                isItemSelected(item, selectedItem) && (
                                  <SelectedIcon />
                                )}
                            </Link>
                          </li>
                        );
                      })}
                    </ul>
                  );
                })}
              </div>
              {pinnedItems && pinnedItems.length > 0 && (
                <ul className="divider">
                  {pinnedItems.map((pinnedItem, index) => {
                    const { className, selected, active, to, href, onClick } =
                      pinnedItem;
                    // Offset to account for the scrollable/non-pinned items and ensure all indexes unique across both sets of items
                    const relativeIndex = index + countOfAllItems;
                    return (
                      <li key={`pinned-${relativeIndex}`}>
                        <Link
                          {...getItemProps({
                            item: pinnedItem,
                            index: relativeIndex,
                            to,
                            href,
                            onClick: (e) => {
                              e.preventDefault();
                              onClick?.();
                            },
                          })}
                          className={cn('menuitem', className, {
                            highlighted: relativeIndex === highlightedIndex,
                            selected,
                            active:
                              active ||
                              isItemSelected(pinnedItem, selectedItem),
                          })}
                        >
                          {pinnedItem.label}
                          {selected && <SelectedIcon />}
                        </Link>
                      </li>
                    );
                  })}
                </ul>
              )}
            </MenuWrapper>
          </div>
        );
      }}
    </Downshift>
  );
}

export default Dropdown;

export { Downshift };
