import { cn } from '../helpers/cn';

export enum LogoSize {
  Tiny = 'tiny',
  XSmall = 'xs',
  Small = 'sm',
  Medium = 'md',
  Large = 'lg',
  Full = 'full',
  SitesHero = 'sitesHero',
}

export const logoSizeCN = (size: LogoSize) => {
  switch (size) {
    case LogoSize.Tiny:
      return 'tw-w-5 tw-rounded-[2px] tw-text-xs tw-leading-normal';
    case LogoSize.XSmall:
      // Matches a standard Avatar component size
      return 'tw-w-10 tw-rounded-sm tw-text-lg tw-leading-normal';
    case LogoSize.Small:
      return 'tw-w-16 tw-rounded-lg tw-text-[28px] tw-leading-normal';
    case LogoSize.Medium:
      return cn(
        // Inner-height of the OrganizationOverviewHero card
        'tw-w-32',
        'tw-rounded-lg',
        'tw-text-[56px]',
        'tw-leading-chill'
      );
    case LogoSize.Large:
      return cn(
        // Maximum height of the TeamOverviewHero card
        'tw-w-52 [@media(min-width:1330px)]:tw-w-[188px]',
        'tw-rounded-lg',
        'tw-text-[80px]',
        'tw-leading-chill'
      );
    case LogoSize.Full:
      return 'tw-w-full tw-rounded-lg tw-text-[80px] tw-leading-chill';
    case LogoSize.SitesHero:
      // Height of the sites hero area on wider viewports
      return 'tw-w-[83px] tw-rounded-lg tw-text-[32px] tw-leading-chill';
  }
};

export const imageCN = cn(
  'tw-block',
  'tw-h-full',
  'tw-w-full',
  'tw-object-cover'
);
export const iconCN = cn(
  imageCN,
  'tw-text-white',
  'tw-flex',
  'tw-items-center',
  'tw-justify-center',
  'tw-font-semibold'
);

export const getAltText = (prefix: string, name?: string) =>
  name && `${prefix} for ${name}`;
