import { type RefObject, useEffect, useState } from 'react';

const isWithinOnePixel = (a: number, b: number) => Math.abs(a - b) <= 1;

export const useDropdownAlignment = (
  dropdownContentRef: RefObject<HTMLElement>,
  preferredAlign: 'left' | 'right' = 'left'
) => {
  const [dropdownAlign, setDropdownAlign] = useState(preferredAlign);

  useEffect(() => {
    setDropdownAlign(preferredAlign);
  }, [preferredAlign]);

  useEffect(() => {
    const { current: dropdownContent } = dropdownContentRef;

    if (!dropdownContent) {
      return;
    }

    const handleIntersection: IntersectionObserverCallback = ([
      intersectionEntry,
    ]) => {
      const { boundingClientRect, intersectionRect, intersectionRatio } =
        intersectionEntry;
      const { left: visibleLeft, right: visibleRight } = intersectionRect;
      const { left: elementLeft, right: elementRight } = boundingClientRect;

      if (intersectionRatio === 0) {
        setDropdownAlign(preferredAlign);
        return;
      }

      // Dropdown content is fully visible, so no need to adjust alignment
      if (intersectionRatio === 1) {
        return;
      }

      const isLeftInView = isWithinOnePixel(visibleLeft, elementLeft);
      const isRightInView = isWithinOnePixel(visibleRight, elementRight);

      if (!isLeftInView) {
        setDropdownAlign('left');
        return;
      }

      if (!isRightInView) {
        setDropdownAlign('right');
        return;
      }

      setDropdownAlign(preferredAlign);
    };

    // Listen for when dropdown content becomes only partially visible in the viewport (threshold of 0 is crossed)
    const observer = new IntersectionObserver(handleIntersection, {
      threshold: [0, 1],
    });

    observer.observe(dropdownContent);

    return () => observer.disconnect();
  }, [dropdownContentRef, preferredAlign]);

  return dropdownAlign;
};
