import { theme } from '@netlify/tailwind-config/theme';
import type { TippyProps } from '@tippyjs/react';
import OriginalTippy from '@tippyjs/react';

import { cn } from '../helpers/cn';
import { remToPxNumber } from '../helpers/remToPxNumber';
import { AccessibleIcon } from '../Icon/AccessibleIcon';
import type { IconName } from '../Icon/types';
import {
  hideOnEsc,
  hideOnPopperBlur,
  manageFocusInPopover as manageFocusInPopoverPlugin,
  type TooltipPluginProps,
} from './TippyPlugins';

interface Intent {
  iconName: IconName;
  buttonClassName: string;
  title: string;
}

const Tippy = OriginalTippy as React.ForwardRefExoticComponent<
  TippyProps & TooltipPluginProps
>;

const intents: Record<string, Intent> = {
  info: {
    iconName: 'info-round',
    buttonClassName: cn(
      'tw-text-gray-darkest',
      'hover:tw-text-black',
      'dark:tw-text-gray-lightest',
      'dark:hover:tw-text-white'
    ),
    title: 'More information',
  },
  warning: {
    iconName: 'warning-triangle',
    buttonClassName: cn(
      'tw-text-gold-darkest',
      'hover:tw-text-gold-darker',
      'dark:tw-text-gold',
      'dark:hover:tw-text-gold-darker'
    ),
    title: 'Alert',
  },
  error: {
    iconName: 'warning-round',
    buttonClassName: cn(
      'tw-text-red-darkest',
      'hover:tw-text-red-darker',
      'dark:tw-text-red',
      'dark:hover:tw-text-red-darker'
    ),
    title: 'Error',
  },
};

const tooltipBtnCN = (className: Intent['buttonClassName']) =>
  cn(
    '!tw-bg-transparent',
    'tw-border-none',
    'tw-rounded-full',
    'tw-inline-flex',
    'tw-p-0',
    'tw-outline-0',
    'tw-relative',
    'tw-h-4',
    'tw-w-4',
    'tw-top-0.5',
    'focus-visible:tw-ring-2',
    'tw-cursor-help',
    className
  );

const tippyCN = ({
  floating,
  nowrap,
  className,
}: Pick<TooltipProps, 'floating' | 'nowrap' | 'className'>) =>
  cn(
    'tw-text-white',
    'tw-leading-normal',
    'tw-bg-black',
    'tw-rounded-lg',
    'tw-py-3',
    'tw-px-4',
    'tw-text-sm',
    floating &&
      `tw-translate-z-0 tw--translate-y-1/2 tw-translate-x-[calc(0-(theme(spacing.6)+theme(spacing.1)))]`,
    nowrap && 'tw-whitespace-nowrap',
    'code:tw-text-white',
    'code:tw-p-0',
    'code:tw-bg-transparent',
    'dark:tw-bg-gray-darker',
    'dark:tw-border',
    'dark:tw-border-gray-dark',
    className
  );

// see all Tippy option props here: https://kabbouchi.github.io/tippyjs-v4-docs/all-options/
export interface TooltipProps
  extends Omit<TippyProps & TooltipPluginProps, 'content'> {
  description: string | TippyProps['content'];
  floating?: boolean;
  nowrap?: boolean;
  outset?: string;
  intent?: 'info' | 'warning' | 'error';
  iconName?: IconName;
  buttonClassName?: string;
  title?: string;
  onClick?: () => void;
  id?: string;
}

export const Tooltip = ({
  floating = false,
  nowrap = false,
  outset,
  children,
  intent = 'info',
  iconName = intents[intent].iconName,
  buttonClassName = intents[intent].buttonClassName,
  title = intents[intent].title,
  appendTo = 'parent',
  description,
  maxWidth = 416,
  offset = [0, remToPxNumber(theme('spacing.2'))],
  visible,
  className,
  onClickOutside,
  onClick,
  id,
  manageFocusInPopover = appendTo !== 'parent',
  ...rest
}: TooltipProps) => {
  const otherProps =
    typeof visible !== 'undefined'
      ? { onClickOutside, visible }
      : {
          trigger: 'click mouseenter focusin',
        };

  return (
    <Tippy
      arrow={false}
      className={
        typeof rest.render === 'undefined'
          ? tippyCN({ floating, nowrap, className })
          : undefined
      }
      content={description}
      offset={offset}
      duration={0}
      plugins={[hideOnEsc, hideOnPopperBlur, manageFocusInPopoverPlugin]}
      interactive
      maxWidth={maxWidth}
      appendTo={appendTo}
      popperOptions={{
        modifiers: [
          {
            name: 'flip',
            options: {
              fallbackPlacements: ['top', 'bottom'],
            },
          },
          {
            name: 'preventOverflow',
            options: {
              altAxis: true,
            },
          },
        ],
      }}
      {...otherProps}
      {...rest}
      manageFocusInPopover={manageFocusInPopover}
    >
      {children ? (
        children
      ) : (
        /**
         * This wrapper:
         * 1. Uses `outset` to create some spacing around the tooltipped button
         * 2. Allows the buttons for floating tooltips to appear inline next to
         * other elements. Without this, the button's placement will break.
         */
        <span
          className={cn(outset, floating && 'tw-absolute tw--right-7 tw--mt-8')}
        >
          <button
            id={id}
            className={tooltipBtnCN(buttonClassName)} // must have Tooltip__btn for Tippy to work
            type="button" // important to specify type so that tooltips inside forms don't assume type="submit"
            onClick={(e) => {
              e.preventDefault();
              onClick?.();
            }}
          >
            <AccessibleIcon title={title} name={iconName} />
          </button>
        </span>
      )}
    </Tippy>
  );
};
