import type { AvatarProps } from '../Avatar/Avatar';
import { Avatar } from '../Avatar/Avatar';
import { cn } from '../helpers/cn';

export type AvatarListProps = {
  users: AvatarProps['user'][];
  size?: AvatarProps['size'];
  maxNumber?: number;
  includeHoverTooltips?: boolean;
  additionalTextType?: 'useName' | 'useCalculatedNumber';
  userLabel?: string;
  additionalUsersLabelSingular?: string;
  additionalUsersLabelPlural?: string;
};

const AdditionalUsersTooltipContent = ({
  additionalUsers = [],
  additionalTextType = 'useName',
  additionalUsersLabelSingular,
  additionalUsersLabelPlural,
}: {
  additionalUsers?: AvatarProps['user'][];
  additionalTextType?: string;
  additionalUsersLabelSingular?: string;
  additionalUsersLabelPlural?: string;
}) => {
  const additionalUsersText =
    additionalUsers.length === 1
      ? additionalUsersLabelSingular
      : additionalUsersLabelPlural;

  return (
    <span className="tw-flex tw-flex-col tw-items-center">
      {additionalTextType === 'useName' ? (
        <>
          {additionalUsers.map((user, index = 0) => {
            return (
              <span key={`additional-user-${index}`}>{user?.full_name}</span>
            );
          })}
          {additionalUsersText}
        </>
      ) : (
        <span>
          {additionalUsers.length} {additionalUsersText}
        </span>
      )}
    </span>
  );
};

const listItemClassName = (size?: AvatarProps['size']) =>
  cn(
    'tw-shrink-0',
    'tw-rounded-full',
    'tw-border-white',
    'dark:tw-border-black',
    'tw-border-solid',
    'tw-relative',
    size === 'xs' && 'tw-border',
    size === 'sm' && 'tw-border',
    (size === 'md' || !size) && 'tw-border-2',
    size === 'lg' && 'tw-border-[3px]',
    size === 'xl' && 'tw-border-4'
  );

export function AvatarList({
  users,
  size,
  maxNumber,
  includeHoverTooltips = false,
  additionalTextType = 'useName',
  userLabel,
  additionalUsersLabelSingular,
  additionalUsersLabelPlural,
}: AvatarListProps) {
  const items = maxNumber ? users.slice(0, maxNumber) : users;
  const additionalUsersLength = maxNumber ? users.length - maxNumber : 0;
  const renderAdditionalUsers = additionalUsersLength > 0;
  const additionalUsersLabel = `+${additionalUsersLength}`;

  if (!users || users.length === 0) {
    return null;
  }

  return (
    <ul
      className={cn(
        'tw-flex',
        'tw-flex-row',
        'tw-items-center',
        size === 'xs' && '[&_>_*_+_*]:tw--ml-0.5',
        size === 'sm' && '[&_>_*_+_*]:tw--ml-1',
        (size === 'md' || !size) && '[&_>_*_+_*]:tw--ml-2',
        size === 'lg' && '[&_>_*_+_*]:tw--ml-4',
        size === 'xl' && '[&_>_*_+_*]:tw--ml-6',
        // Override .details ul styles
        '!tw-list-none',
        '!tw-pl-0'
      )}
    >
      {items.map((user) => {
        const nameOrEmail = user?.full_name || user?.email;
        const userTooltipText = `${nameOrEmail}${userLabel ? ` ${userLabel}` : ''}`;

        return (
          <li key={user?.id} className={listItemClassName(size)}>
            <Avatar
              alt
              user={user}
              size={size}
              hoverTooltip={includeHoverTooltips ? userTooltipText : undefined}
            />
          </li>
        );
      })}
      {renderAdditionalUsers && (
        <li key="additional-users" className={listItemClassName(size)}>
          <Avatar
            placeholderText={additionalUsersLabel}
            size={size}
            showInnerRing={false}
            className={cn(
              '!tw-bg-gray-lighter  !tw-text-gray-darker dark:!tw-bg-gray-dark dark:!tw-text-gray-lightest',

              size === 'xs' && '!tw-text-[7px]',
              size === 'sm' && '!tw-text-[10px]',
              !size && '!tw-text-[12px]',
              size === 'md' && '!tw-text-base',
              size === 'lg' && '!tw-text-[24px] md:!tw-text-[32px]',
              size === 'xl' && '!tw-text-[32px] md:!tw-text-[40px]'
            )}
            title={includeHoverTooltips ? undefined : additionalUsersLabel}
            placeholderBackgroundColors={{
              // Rely on the background instead of the gradient
              from: 'transparent',
              to: 'transparent',
            }}
            hoverTooltip={
              includeHoverTooltips ? (
                <AdditionalUsersTooltipContent
                  additionalUsers={users.slice(-additionalUsersLength)}
                  additionalTextType={additionalTextType}
                  additionalUsersLabelPlural={additionalUsersLabelPlural}
                  additionalUsersLabelSingular={additionalUsersLabelSingular}
                />
              ) : undefined
            }
          />
        </li>
      )}
    </ul>
  );
}
