import { cn } from '../helpers/cn';
import type { ImageFormat } from './helpers';
import { generateImageUrl, getSources } from './helpers';

export interface PictureProps {
  alt: string;
  url: string;
  width: number;
  height: number;
  fallbackFormat?: ImageFormat;
  /** Supplied in priority order */
  formats?: ImageFormat[];
  className?: string;
  onError?: (e) => void;
  animated?: boolean;
  cover?: boolean;
  title?: string;
}

export const Picture = ({
  alt,
  url,
  width,
  height,
  formats = ['avif', 'webp'],
  fallbackFormat,
  className,
  onError,
  animated = false,
  cover = false,
  title,
}: PictureProps) => {
  const options = { width, height, cover, animated };
  const sources = getSources({
    // IPX proxy add experimental support for animated webp
    formats: animated ? ['webp'] : formats,
    url,
    options,
  });
  const fallbackSrc = generateImageUrl(url, {
    ...options,
    format: fallbackFormat || animated ? 'gif' : 'png',
  });

  return (
    <picture onError={onError} className={className}>
      {sources.map((source, i) => (
        <source key={`source:${i}`} {...source} />
      ))}
      <img
        src={fallbackSrc}
        alt={alt}
        width={width}
        height={height}
        loading="lazy"
        className={cn('tw-align-top', cover && 'tw-h-full tw-w-full')}
        title={title}
      />
    </picture>
  );
};
