import * as React from 'react';

import type { IconName } from '../Icon/types';
import { useImportSvg } from '../SvgProvider/SvgProvider';
import { IconNameToPath } from './types';

/**
 * Hook to dynamically import an svg and render it inline
 * @param name - name of the svg to be able to find in /images
 */
export default function useSvg(
  name: IconName
): [boolean, React.ElementType<React.HTMLAttributes<SVGElement>> | null] {
  const [Icon, setIcon] = React.useState<{
    component: React.ElementType<React.SVGProps<SVGSVGElement>>;
  } | null>(null);
  const [loading, setLoading] = React.useState(false);
  const importFunction = useImportSvg();

  React.useEffect(() => {
    let isMounted = true;
    setLoading(true);

    const importIcon = async (): Promise<void> => {
      try {
        const component = (await importFunction(IconNameToPath[name]))
          .ReactComponent;
        if (isMounted) {
          setIcon({ component });
        }
      } catch (err) {
        console.error(err);
      } finally {
        if (isMounted) {
          setLoading(false);
        }
      }
    };

    importIcon();

    return () => {
      isMounted = false;
    };
  }, [name, importFunction]);

  return [loading, Icon?.component ?? null];
}
