import { cn } from '../helpers/cn';
import { Badge, BadgeVariant } from './Badge';

export interface BetaBadgeProps {
  heading?: boolean;
  className?: string;
}

export const BetaBadge = ({ heading, className }: BetaBadgeProps) => {
  const betaBadgeCN = cn(
    heading && 'tw-top-1 !tw-self-start tw-align-top',
    className
  );

  return (
    <Badge as="sup" variant={BadgeVariant.Pop} className={betaBadgeCN}>
      Beta
    </Badge>
  );
};
