import type { Props as ButtonProps } from '../Button/Button';
import { Button } from '../Button/Button';
import { cn } from '../helpers/cn';

type NavigationButtonProps = ButtonProps & {
  active?: boolean;
  large?: boolean;
};

const navigationButtonCN = ({
  active,
  large,
  className,
}: NavigationButtonProps): string =>
  cn(
    'tw-w-auto',
    'tw-font-semibold',
    'tw-px-2',
    'tw-min-h-9',
    'tw-leading-6',
    'tw-text-left',
    'dark:hover:tw-text-teal-lightest',
    'dark:hover:tw-bg-teal-darkest',
    large && 'tw-text-xl',
    active
      ? ['tw-text-black', 'dark:tw-text-white']
      : 'tw-text-gray-darker dark:tw-text-gray-light',
    className
  );

export const NavigationButton = ({
  active,
  large,
  className,
  ...rest
}: NavigationButtonProps) => {
  return (
    <Button
      level="tertiary"
      className={navigationButtonCN({
        active,
        large,
        className,
      })}
      {...rest}
    />
  );
};
