import * as React from 'react';

import { cn } from '../helpers/cn';
import { DecorativeIcon } from '../Icon/DecorativeIcon';

interface NavigationProps {
  align?: 'start' | 'center' | 'end';
  children?: React.ReactNode;
  className?: string;
  ordered?: boolean;
  /* Determines if there should be a separator ('/') between every non-last item  */
  separator?: boolean;
  itemClassName?: string;
  id?: string;
}

export const Navigation = ({
  align,
  children,
  className,
  ordered = false,
  separator = false,
  itemClassName,
  ...rest
}: NavigationProps) => {
  if (!children) return null;

  const alignmentCN = () => {
    switch (align) {
      case 'start':
        return 'tw-items-start';
      case 'end':
        return 'tw-items-end';
      case 'center':
      default:
        return 'tw-items-center';
    }
  };

  const listCN = cn(
    'tw-flex',
    'tw-flex-wrap',
    alignmentCN(),
    'tw-list-none',
    'tw--mx-3',
    'tw-my-0',
    'tw-p-0',
    className
  );

  const itemCN = cn('tw-flex', 'tw-items-center', 'tw-m-1', itemClassName);

  const validChildren = React.Children.toArray(children).filter((child) =>
    React.isValidElement(child)
  );

  const List = ordered ? 'ol' : 'ul';
  const childrenCount = validChildren.length - 1;

  return (
    <List className={listCN} {...rest}>
      {validChildren.map((child, index) => {
        if (!child) {
          return null;
        }

        return (
          <li className={itemCN} key={`navigation-${index}`}>
            {child}
            {separator && index !== childrenCount && (
              <DecorativeIcon
                name="nav-separator"
                className="tw-mx-2 tw-hidden tw-shrink-0 tw-text-gray-light lg:tw-inline-block"
                data-testid="navigation-separator"
                width={11}
                height={22}
              />
            )}
          </li>
        );
      })}
    </List>
  );
};
