import { createContext, type ReactNode, useContext } from 'react';

import type { IconPath } from '../Icon/types';

interface SvgProviderProps {
  children: ReactNode;
  importSvg: (name: IconPath) => Promise<{
    ReactComponent: React.ElementType<React.SVGProps<SVGSVGElement>>;
  }>;
}

const SvgContext = createContext<SvgProviderProps['importSvg'] | undefined>(
  undefined
);

export const SvgProvider = ({ children, importSvg }: SvgProviderProps) => {
  return (
    <SvgContext.Provider value={importSvg}>{children}</SvgContext.Provider>
  );
};

export const useImportSvg = () => {
  const svgContext = useContext(SvgContext);

  if (!svgContext) {
    throw new Error('useSvg must be used within a SvgProvider');
  }

  return svgContext as SvgProviderProps['importSvg'];
};
