import {
  type ComponentPropsWithRef,
  createContext,
  type ReactNode,
  useContext,
} from 'react';

export interface LinkLikeComponentProps extends ComponentPropsWithRef<'a'> {
  to?: string;
  [key: string]: unknown;
}

export interface LinkProviderProps {
  /** A render function that returns the link element used by all `netlify-react-ui` links */
  renderLink: (props: LinkLikeComponentProps) => JSX.Element;
  children: ReactNode;
}

const LinkContext = createContext<LinkProviderProps['renderLink'] | undefined>(
  undefined
);

export const LinkProvider = ({ children, renderLink }: LinkProviderProps) => {
  return (
    <LinkContext.Provider value={renderLink}>{children}</LinkContext.Provider>
  );
};

export const useLink = () => {
  const linkContext = useContext(LinkContext);

  if (!linkContext) {
    throw new Error('useLink must be used within a LinkProvider');
  }

  return linkContext;
};
