import { cn } from '../helpers/cn';
import { Badge, BadgeVariant } from './Badge';

export interface TrialBadgeProps {
  heading?: boolean;
  planName?: string;
  featureName?: string;
  className?: string;
}

export const TrialBadge = ({
  heading,
  planName = 'Pro',
  featureName = 'This feature',
  className,
}: TrialBadgeProps) => {
  const trialBadgeCN = cn(
    heading && 'tw-top-1 !tw-self-start tw-align-top',
    className
  );

  return (
    <Badge
      as="sup"
      variant={BadgeVariant.Upgrade}
      className={trialBadgeCN}
      title={`${featureName} is a ${planName} feature`}
    >
      {planName} feature
    </Badge>
  );
};
