import type { DecorativeIconProps } from './DecorativeIcon';
import { DecorativeIcon } from './DecorativeIcon';

export interface AccessibleIconProps extends DecorativeIconProps {
  /** Human readable title for the screenreader describing the icon */
  title: string;
}

/**
 * An icon that is visible to screenreaders and should be interacted with
 * Accessibility follows the SVG pattern 5 of
 * {@link https://www.deque.com/blog/creating-accessible-svgs/}
 */
export function AccessibleIcon(props: AccessibleIconProps) {
  return <DecorativeIcon aria-hidden={undefined} role="img" {...props} />;
}
