import * as React from 'react';

import { cn } from '../helpers/cn';
import { getActualSizeForNumberValue } from './helpers';
import type { IconName } from './types';
import useSvg from './useSvg';

export interface DecorativeIconProps extends React.SVGAttributes<SVGElement> {
  /** Name of the icon that maps to the svg file */
  name: IconName;
}

/**
 * An icon that is hidden from screenreaders and serves decorative purposes
 */
export function DecorativeIcon({
  className,
  style,
  name,
  width,
  height = width,
  ...rest
}: DecorativeIconProps) {
  const [loading, Icon] = useSvg(name);

  const iconWidth = getActualSizeForNumberValue(width);
  const iconHeight = getActualSizeForNumberValue(height);

  if (loading || !Icon) {
    return (
      <span
        className={cn(className, 'tw-inline-block')}
        style={{
          height: iconHeight,
          width: iconWidth,
        }}
      />
    );
  }

  return (
    <Icon
      // We set the styles in CSS this way so that className props including e.g. `tw-w-6` will be more specific and override any default sizing
      style={
        {
          ...style,
          '--icon-width': iconWidth,
          '--icon-height': iconHeight,
        } as React.CSSProperties
      }
      aria-hidden
      className={cn(
        'scalable-icon tw-inline !tw-fill-current tw-align-baseline',
        className
      )}
      // Reset any width and height properties that may have been baked in to the SVG itself to favor the scalable sizes
      height={undefined}
      width={undefined}
      {...rest}
    />
  );
}
