import { Badge, BadgeVariant } from './Badge';

export const PublishedPinnedBadge = ({
  published,
  pinned,
}: {
  published?: boolean;
  pinned?: boolean;
}) => {
  if (published) {
    return (
      <Badge
        variant={BadgeVariant.Pop}
        className="!tw-top-0 tw-ml-1 tw-align-middle tw-normal-case"
        {...(pinned ? { iconName: 'pin' } : {})}
      >
        {pinned ? 'Pinned' : 'Default'}
      </Badge>
    );
  }
  return null;
};
