export const isAvatarUrlCustomUpload = (url: string) => {
  try {
    const imageCdnDomain = process.env.NETLIFY_IMAGES_CDN_DOMAIN;
    return new URL(url).host === imageCdnDomain;
  } catch {
    return false;
  }
};

export const getAvatarSrcFromUser = (user) => {
  const avatar = user?.avatar;
  const avatarHref = avatar?.href;
  const avatarUrl = user?.avatar_url;
  const userMetadata = user?.user_metadata;

  return avatarHref || avatar || avatarUrl || userMetadata?.avatar;
};
