import { Avatar, type AvatarUser } from '../Avatar/Avatar';
import { Dropdown, type IDropdownItem } from '../Dropdown/Dropdown';

interface UserDropdownProps {
  user?: AvatarUser;
  className?: string;
  onLogout?: () => void;
  alignRight?: boolean;
  userPath: string;
  links?: IDropdownItem[];
}

export function UserDropdown({
  user,
  className,
  onLogout,
  userPath,
  alignRight = true,
  links = [],
}: UserDropdownProps) {
  if (!user) {
    return null;
  }

  const { full_name: name = '', email = '' } = user || {};

  const dropdownOptions = [
    {
      label: (
        <>
          {/* Data attributes are used for identifying the user in the Netliusers extension */}
          <strong data-user-name={name} className="h4 tw-block">
            {name}
          </strong>
          <small data-user-email={email} className="meta">
            {email}
          </small>
        </>
      ),
      to: userPath,
    },
    ...links,
    {
      label: 'Sign out',
      onClick: onLogout,
      className: 'danger',
    },
  ].filter(Boolean);

  return (
    <Dropdown
      alignRight={alignRight}
      className={className}
      noCaret
      noCheck
      variant="bare"
      toggle={{
        ariaLabel: `User ${email}`,
        className:
          'tw-relative tw-rounded-full tw-w-9 tw-h-9 hover:tw-ring-2 focus:tw-ring-2',
        label: (
          <Avatar
            user={user}
            dropdown={true}
            className="tw-border tw-border-white dark:tw-border-bgApp"
          />
        ),
      }}
      menu={{
        className: 'tw-top-10 tw-mt-1',
      }}
      items={dropdownOptions}
    />
  );
}
